<template>
    <div style="padding: 20px;">
        <el-form ref="form" :rules="rules" :model="form" label-width="80px" style="width: 400px;">
            <el-form-item label="姓名" prop="name">
                <el-input v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item label="手机号" prop="phone">
                <el-input v-model="form.phone"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
                <el-input type="password" v-model="form.password"></el-input>
            </el-form-item>

            <el-form-item label="性别">
                <el-radio-group v-model="form.gender">
                    <el-radio :label="1">男</el-radio>
                    <el-radio :label="2">女</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="头像" prop="avatarUrl">

                <el-upload :headers="uploadHeaders" class="avatar-uploader" :action="uploadUrl" :show-file-list="false"
                    :on-success="handleAvatarSuccess">
                    <img v-if="form.avatarUrl" :src="form.avatarUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>

            </el-form-item>


            <el-form-item label="部门" prop="depIdList">
                <el-cascader style="width: 70%;" v-model="form.depIdList" filterable :props="tableProps"
                    :options="tableData" :show-all-levels="false"></el-cascader>

            </el-form-item>

            <el-form-item label="入职时间" prop="entryDate">
                <el-date-picker :picker-options="pickerOptions0" format="yyyy-MM-dd" value-format="yyyy-MM-dd"
                    v-model="form.entryDate" type="date" placeholder="选择日期">
                </el-date-picker>
            </el-form-item>
            
            <el-form-item v-for="(item, index) in form.userFieldDataList" :label="item.name" :prop="item.key">
                <template v-if="item.type === 'Number'">
                    <el-input v-model="form[item.key]" type="number"></el-input>
                </template>

                <template v-if="item.type === 'String'">
                    <el-input v-model="form[item.key]"></el-input>
                </template>
                <template v-if="item.type === 'Date'">
                    <el-date-picker format="yyyy-MM-dd" style="width: 100%;" value-format="yyyy-MM-dd" v-model="form[item.key]" type="date"
                        placeholder="选择日期">
                    </el-date-picker>
                </template>
                <template v-if="item.type === 'DateTime'">
                    <el-date-picker format="yyyy-MM-dd HH:mm:ss" style="width: 100%;" value-format="yyyy-MM-dd HH:mm:ss" v-model="form[item.key]"
                        type="datetime" placeholder="选择日期">
                    </el-date-picker>
                </template>

                <template v-if="item.type === 'Time'">
                    <el-time-picker arrow-control style="width: 100%;" value-format="HH:mm:ss" format="HH:mm:ss" 
                        v-model="form[item.key]">
                    </el-time-picker>
                </template>


                <template v-if="item.type === 'Select'">
                    <el-select v-model="form[item.key]" placeholder="请选择" style="width: 100%;">
                        <el-option v-for="item1 in jsonStringToJSON(item.configuration)" :key="item1.key"
                            :label="item1.value" :value="item1.key">
                        </el-option>
                    </el-select>
                </template>
                

                <template v-if="item.type === 'MultiSelect'">
                    <el-select multiple  v-model="form[item.key]" placeholder="请选择" style="width: 100%;">
                        <el-option v-for="item1 in jsonStringToJSON(item.configuration)" 
                            :label="item1.value" :value="item1.key">
                        </el-option>
                    </el-select>
                </template>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit">保存</el-button>
                <el-button @click="back">返回</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import { getOrgTreeAll } from '@/api/org'
import { createUser } from '@/api/user'
import { queryAllField } from '@/api/userField.js'
import constant from '@/api/constant'


export default {
    name: "index",
    components: {},
    data() {
        var checkPhone = (rule, value, callback) => {
            if (!value) {
                return callback(new Error('请输入手机号'));
            }
            if (!/^\d{11}$/.test(value)) {
                return callback(new Error('请输入手机号'));
            }
            callback();
        };
        return {
            uploadHeaders: {
                cxygzl: localStorage.getItem("cxygzl")
            },
            pickerOptions0: {
                disabledDate(time) {
                    return time.getTime() > Date.now() - 8.64e6;//如果没有后面的-8.64e6就是不可以选择今天的
                }
            },
            rules: {
                name: [
                    { required: true, message: '请输入姓名', trigger: 'blur' },
                    { min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: '请输入密码', trigger: 'blur' },
                    { min: 6, max: 20, message: '长度在 6 到 20 个字符', trigger: 'blur' }
                ],
                phone: [
                    { required: true, message: '请输入手机号', trigger: 'blur' },
                    { validator: checkPhone, trigger: 'blur' }
                ],
                avatarUrl: [
                    { required: true, message: '请上传头像', trigger: 'blur' }
                ],
                depIdList: [
                    { required: true, message: '请选择部门', trigger: 'blur' }
                ],
                entryDate: [
                    { required: true, message: '请选择入职时间', trigger: 'blur' }
                ]
            },
            uploadUrl: constant.BASE_URL + "/file/upload",
            tableData: [],
            tableProps: {
                value: 'id',
                label: 'name',
                checkStrictly: true
            },
            form: {
                name: '',
                depIdList: [],
                entryDate: new Date(),
                gender: 1,
                password: '',
                avatarUrl: '',
                phone: ''
            }
        }
    },
    watch: {

    },
    mounted() {
        this.getOrg();
        var t =this;
        queryAllField().then(res => {
            var data = res.data;

            t.form.userFieldDataList = data;

            for (var item of data) {
                if (item.required) {


                    t.$set(t.rules, item.key,
                        [
                            { required: true, message: '字段' + item.name + '必填', trigger: 'blur' }
                        ]
                    )
                }
            }

        })
    },
    methods: {
        jsonStringToJSON(v) {
            return JSON.parse(v);
        },
        back() {
            this.$router.push("/org/user")
        },
        getOrg() {
            var t = this;
            getOrgTreeAll({ deptId: 0, isDept: true }).then(rsp => {
                t.tableData = rsp.data;
            }).catch(err => {

                this.$message.error(err.response.data)
            })
        },
        handleAvatarSuccess(response, file, fileList) {
            console.log(response)
            console.log(file)
            console.log(fileList)
            this.form.avatarUrl = response;
        },
        onSubmit() {
            var param=JSON.parse(JSON.stringify(this.form))
            var t = this;
            this.$refs.form.validate((valid) => {
                if (valid) {
                    param.depId = param.depIdList[param.depIdList.length - 1];
                   
                   param.fieldData=t.form;
                    createUser(param).then(res => {
                        t.$message.success(res.data);
                        t.back();
                    }).catch(err => {

                        this.$message.error(err.response.data)
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        }
    }
}
</script>

<style scoped>
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

.avatar-uploader .el-upload:hover {
    border-color: #409EFF;
}

.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 100px;
    height: 100px;
    line-height: 100px;
    text-align: center;
    border: 1px solid #8c939d;
}

.avatar {
    width: 100px;
    height: 100px;
    display: block;
}</style>
