import request from '@/api/request.js'


// 保存字段
export function saveField(param) {
  return request({
    url: '/userField/save',
    method: 'post',
    data: param
  })
}
 

// 查询所有的字段
export function queryAllField(param) {
  return request({
    url: '/userField/queryAll',
    method: 'get',
    params: param
  })
}
  
export default {
  saveField,queryAllField
}
